<template>
  <v-container
    fill-height
    fluid
    grid-list-xl
  >
    <v-row justify="center">
      <v-col cols="12">

        <v-card color="green" class="white--text pa-3">
          <v-row>
            <v-col cols="10">
              <h3 class="title font-weight-light"> Agenda </h3>
              <h4 class="category font-weight-thin mb-0 "> Visualize a agenda do estabelecimento.</h4>
            </v-col>
          </v-row>
        </v-card>

        <v-card>
          <v-card-text>
            <div>
          <v-sheet
            tile
            height="54"
            color="grey lighten-3"
            class="d-flex mb-5 sheet-title pt-2"
          >
            <v-btn
              icon
              class="ma-2"
              @click="$refs.calendar.prev()"
            >
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
            <v-select
              v-model="type"
              :items="types"
              dense
              outlined
              hide-details
              label="Filtro"
            ></v-select>

            <v-spacer></v-spacer>

            <v-btn
              icon
              class="ma-2"
              @click="$refs.calendar.next()"
            >
              <v-icon>mdi-chevron-right</v-icon>
            </v-btn>
          </v-sheet>

          <v-sheet height="600">
            <v-calendar
              ref="calendar"
              v-model="value"
              :weekdays="weekday"
              :type="type"
              :events="events"
              :event-overlap-mode="mode"
              :event-overlap-threshold="30"
              :event-color="getEventColor"
              @click:event="showEvent"
            ></v-calendar>
          </v-sheet>
        </div>
          </v-card-text>
        </v-card>

        <!-- calendar -->
      </v-col>
    </v-row>

    <v-dialog v-model="showDialogDetails" width="500px">
      <v-card>
        <v-card-title class="primary white--text">
          Informaes sobre servio agendado
        </v-card-title>

        <v-card-text class="mt-5">
          <v-row>
            <v-col
              sm="6"
            >
              <strong> Estabelecimento: </strong> {{eventScheduled.all_data.establishment.name}}
            </v-col>

            <v-col
              sm="6"
            >
              <strong> Profissional: </strong> {{eventScheduled.all_data.professional.name}}
            </v-col>
          </v-row>

          <v-row>
            <v-col
              sm="6"
            >
              <strong> Servio: </strong> {{eventScheduled.all_data.product.title}}
            </v-col>
            <v-col
              sm="6"
            >
              <strong> Cliente: </strong> {{eventScheduled.all_data.user.name}}
            </v-col>
          </v-row>

          <v-row>
            <v-col
              sm="6"
            >
              <strong> Horrio de Incio:</strong> {{ parseDate(eventScheduled.all_data.start) }}
            </v-col>
            <v-col
              sm="6"
            >
              <strong> Horrio de Termino:</strong> {{ parseDate(eventScheduled.all_data.end) }}
            </v-col>
          </v-row>

        </v-card-text>

        <v-card-actions>
          <v-btn
            roudend
            class="px-5 info full-width"
            @click="showDialogDetails = false"
          >
            Sair
          </v-btn>
        </v-card-actions>

      </v-card>

    </v-dialog>
  </v-container>
</template>

<script>
  import { securedAxiosInstance } from '../plugins/axios'
  import moment from 'moment';
  import { format } from 'path';

  export default {
    components: {},

    mounted() {
      this.getEvents()
    },

    data: () => ({
      type: 'month',
      types: ['month', 'week', 'day', '4day'],
      mode: 'stack',
      weekday: [0, 1, 2, 3, 4, 5, 6],
      weekdays: [
        { text: 'Dom - Sab', value: [0, 1, 2, 3, 4, 5, 6] },
        { text: 'Seg - Dom', value: [1, 2, 3, 4, 5, 6, 0] },
        { text: 'Seg - Sex', value: [1, 2, 3, 4, 5] },
        { text: 'Seg, Qua, Qui', value: [1, 3, 5] },
      ],
      value: '',
      events: [],
      colors: ['blue', 'indigo', 'deep-purple', 'cyan', 'green', 'orange', 'grey darken-1'],
      names: ['Meeting', 'Holiday', 'PTO', 'Travel', 'Event', 'Birthday', 'Conference', 'Party'],
      showDialogDetails: false,
      eventScheduled: {
        all_data: {
          establishment: {
            name: null
          },
          professional: {
            name: null
          },
          product: {
            name: null
          },
          user: {
            name: null
          },
          start: null,
          end: null
        },
      }
    }),

    methods: {
      getEvents () {
          const events = []
          this.getSchedulings()
      },

      getEventColor (event) {
        return event.color
      },

      rnd (a, b) {
        return Math.floor((b - a + 1) * Math.random()) + a
      },

      formatDate (a, withTime) {
        let newDate = new Date(a)
        let date = `${newDate.getFullYear()}-${newDate.getMonth() + 1}-${newDate.getDate()} ${newDate.getHours()}:${newDate.getMinutes()}`
        return date

      },

      async getSchedulings() {
        let response = null
        let events = []
        await securedAxiosInstance.get(`/api/v1/establishments/${this.$store.state.selectedEstablishment.id}/schedulings`,
          {
            params: { access_token: localStorage.getItem('access_token') }
          }
        )
        .then((result) => {
          response = result
        }).catch((err) => {
          response = err
        });

        if ( response.status == 200 ) {

          response.data.forEach(event => {
            events.push({
              name: `${event.user.name} - ${event.product.title}`,
              start: this.formatDate(event.start, true),
              end: this.formatDate(event.end, true),
              color: this.colors[this.rnd(0, this.colors.length - 1)],
              all_data: event
            })
          });

          this.events = events
        }
      },

      showEvent({ event }) {
        this.showDialogDetails = true
        this.eventScheduled = event
      },

      parseDate( date ) {
        return moment(date).format('DD/MM HH:mm')
      }
    }

  }
</script>


<style scoped>

  .cursor-type {
    cursor: pointer;
  }

  .sheet-title {
    height: 70px !important;
  }

  .full-width {
    width: 100%;
  }
</style>